import { Component } from 'react';
import { createPage, base, ajax, high, toast } from 'nc-lightapp-front';

let { NCButton, NCModal, NCCheckbox } = base;

class Hpf002 extends Component {
    constructor(props) {
        super(props);
        this.param = { 'action': 'init', 'pk_org': this.props.data.m_sysinitvo.pk_org };
        this.indatatype = [{}];
        this.initModal(this.param);
        this.state = { checkboxList: this.indatatype };
        this.valueChange = props.valueChange;
        this.data = props.data;
        this.editMode = props.editMode;
    }
    initModal(param) {
        ajax({
            url: '/nccloud/hpf/refs/hpf002.do',
            data: { param },
            async: false,
            success: (res) => {
                let { success, data } = res;
                if (success) {
                    this.indatatype = data;
                }
            }
        });
        return this.indatatype;
    }
    onChange = (key, checked, a, b, c, d) => {
        this.param[key] = checked;
        if (this.editMode == 'edit') {
            this.param.action = 'update';
            //选择同步物料档案时，物资分类一并勾选
            let checkboxList = this.state.checkboxList.map(item => {
                if (item.key == key) {
                    item.checked = checked ? 'Y' : 'N';
                }
                return item;
            });
            if (key == 'doc02' && !checked) {
                checkboxList = checkboxList.map(item => {
                    if (item.key == 'doc03') {
                        item.checked = 'N';
                        this.param['doc03'] = checked;
                    }
                    return item;
                });
            }
            if (key == 'doc03' && !checked) {
                checkboxList = checkboxList.map(item => {
                    if (item.key == 'doc02') {
                        item.checked = 'N';
                        this.param['doc02'] = checked;
                    }
                    return item;
                });
            }
            if (key == 'doc03' && checked) {
                checkboxList = checkboxList.map(item => {
                    if (item.key == 'doc02') {
                        item.checked = 'Y';
                        this.param['doc02'] = checked;
                    }
                    return item;
                });
            }
            this.setState({
                checkboxList
            });
        }
    };
    render() {
        let onClick = () => {
            this.valueChange();
            this.setState({ checkboxList: this.initModal(this.param) });
        };
        let { checkboxList } = this.state;
        return (
            <NCModal show={true}>
                <NCModal.Header><NCModal.Title>选择要同步的外部档案</NCModal.Title></NCModal.Header>
                <NCModal.Body>
                    <div className='basic-container'>
                        {
                            checkboxList.map((element) => (
                                <div className='checkbox-wrapper' style={{ marginTop: '10px', marginBottom: '10px' }}>
                                    <NCCheckbox disabled={this.editMode != 'edit'} checked={element.checked == 'Y'} onChange={(checked) => { this.onChange(element.key, checked); }} />
                                    <span>{element.value}</span>
                                </div>)
                            )
                        }
                    </div>
                </NCModal.Body>
                <NCModal.Footer>
                    <span><NCButton onClick={onClick}>确定</NCButton></span>
                </NCModal.Footer>
            </NCModal>
        );
    }
}
export default Hpf002;
